/*  css for contact page */




body {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica,
      Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  }
  
  .main-container {
    padding: 30px;
    display: flex;
  }
  
  /* HEADING */
  
  .heading {
    text-align: center;
  }
  
  .heading__title {
    font-weight: 600;
  }
  
  .heading__credits {
    margin: 10px 0px;
    color: #888888;
    font-size: 25px;
    transition: all 0.5s;
  }
  
  .heading__link {
    text-decoration: none;
  }
  
  .heading__credits .heading__link {
    color: inherit;
  }
  
  /* CARDS */
  
  .cards-ct {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  
  .card-ct {

    display: flex;
    margin: 20px;
    padding: 20px;
    width: 455px;
    height: 110px;
    grid-template-rows: 20px 50px 1fr 50px;
    border-radius: 10px;
    box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.25);
    transition: all 0.2s;
  }
  
  .card-ct:hover {
    box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.4);
    transform: scale(1.01);
  }
  
  .card__link,
  .card__exit,
  .card__icon {
    position: relative;
    text-decoration: none;
    color: rgba(255, 255, 255, 0.9);
  }
  
  .card__link::after {
    position: absolute;
    top: 25px;
    left: 0;
    content: "";
    width: 0%;
    height: 3px;
    background-color: rgba(255, 255, 255, 0.6);
    transition: all 0.5s;
  }
  
  .card__link:hover::after {
    width: 100%;
  }
  
  .card__exit {
    grid-row: 1/2;
    justify-self: end;
  }
  
  .card__icon {
    grid-row: 2/3;
    font-size: 30px;
    padding: 10px;
  }
  
  .card__title {
    grid-row: 3/4;
    font-weight: 400;
    color: #ffffff;
  }
  
  .card__apply {
    grid-row: 4/5;
    align-self: center;
  }
  
  /* CARD BACKGROUNDS */
  
  .card-1 {
    background: radial-gradient(#50ecfa, #61bff1);
    color: #ffffff;
  }
  
  .card-2 {
    background: radial-gradient(#fbc1cc, #fa99b2);
    color: #ffffff;
  }
  
  .card-3 {
    background: radial-gradient(#76b2fe, #b69efe);
    color: #ffffff;
  }
  
  .card-4 {
    background: radial-gradient(#60efbc, #58d5c9);
    color: #ffffff;
  }
  
  .card-5 {
    background: radial-gradient(#f588d8, #c0a3e5);
    color: #ffffff;
  }
  
  /* RESPONSIVE */
  
  @media (max-width: 1600px) {
    .cards-ct {
      justify-content: center;
      flex-direction: column;
    }
  }



  /* contact form of contact page */

#ct-sec{
    height: fit-content;
    margin-top: 30px;
}

  .ct-flex{
display: flex;
  }
  

  .btn-ct{
      width: max-content;
      margin: auto;
  }
  .ct-form-in {
      width: 700px;
      margin: 10px auto;
  }

  .ct-cont{
    min-height: 800px;
  }


  @media only screen and (max-width: 600px) {
    .main-container {
      padding: 3px;
      display: flex;
      flex-wrap: wrap;
  }
  .card-ct {
    display: flex;
    margin: 20px;
    padding: 20px;
    width: 400px;
    height: 110px;
    grid-template-rows: 20px 50px 1fr 50px;
    border-radius: 10px;
    box-shadow: 0px 6px 10px rgb(0 0 0 / 25%);
    transition: all 0.2s;
}
.ct-form-upper{
  flex-wrap: wrap;
}
.ct-cont {
  min-height: 800px;
  padding: 20px;
  margin-bottom: 20px;
}
}